.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.app-header {
  background-color: #c6c6f3;
  color: rgb(14, 148, 126);
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center; /* Añade esta línea */
}

.app-logo {
  height: 50px;
  margin-right: 20px;
}

.app-title {
  text-align: center;
  flex-grow: 1; 
}

.app-content {
  display: flex;
  flex: 1;
}

.sidebar {
  width: 250px;
  background-color: #aa7ee4;
  color: white;
  padding: 20px;
}

.sidebar ul {
  list-style-type: none;
  padding: 0;
}

.sidebar ul li {
  margin-bottom: 10px;
}

.sidebar ul li span {
  font-weight: bold;
}

.sidebar ul ul {
  margin-left: 20px;
}

.sidebar ul ul li {
  cursor: pointer;
}

.sidebar ul ul li:hover {
  text-decoration: underline;
}

.main-content {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #aa7ee4;
  color: white;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}